.ControlMenu{
	position:relative;

	
	background-color:white;
	height:140px;
	top:70px;

	width:1128px;
	left:5%;

	
}

.ControlTab{
	display:inline-block;
	float:left;
	position:relative;
	height:100%;
	width:350px;
	border-right:1px solid rgb(0,0,0,0.3);
	font-family:'Cairo-Light';
	font-weight:300;
	text-align:left;
	padding-left:25px;
	font-size:32px;

	
}

.ControlTabInfo{
	padding-left:230px;
	margin-top:-2px;
	font-size:46px;
	color:green;
	float:right;
	margin-right:24px;
}


.productList{
	position:relative;
	width:1206px;
	left:5%;

	top:100px;
	text-align:left;
	font-family:"muli";
	font-weight:400;
	font-size:40px;

}

.productList img{
	width:120px;
	height:120px;
	position:relative;
	margin-left:20px;
	margin-top:20px;

}

.offertText{
	position:relative;
	padding-left:20px;
	padding-top:20px;
}

.tab{
	left:-40px;
	margin-left:40px;
	margin-top:40px;
	position:relative;
	width:400px;
	height:100%;
	display:inline-block;
	border:1px solid grey;
	background-color:rgb(254,193,7,0.1);
}

.tab:hover{
	cursor:pointer;
	background-color:rgb(254,193,7,0.2);
}





.userInformation{
	position:relative;
	background-color:white;
	top:120px;
	width:1128px;
	left:5%;
	text-align:left;


	font-family:'Cairo-Light';
	font-weight:300;
	text-align:left;
	padding-left:25px;
	font-size:32px;
}


.reviews{
	position:relative;
	display:block;
	top:200px;
	float:left;
	
	font-size:46px;
	font-family:"cairo-light";
	font-weight:400;
	width:1128px;
	left:5%;
	text-align:left;
}
.comment{
	position:relative;
	width:100%;
	background-color:rgb(0,0,0,0.1);
	font-size:16px;
	margin-top:5px;
	padding-left:10px;
}

.comment .name{
	font-size:22px;
}