.BuyPanel{
	width:700px;
	height:400px;
	background: white;
	position:fixed;
	z-index:6;
	margin:0 auto;
	left:50%;
	margin-left:-350px;
	top:110px;
	font-family:'cairo-light';
	font-weight:600;
	font-size:32px;
	overflow:hidden;


}

.title{
	position:relative;
	font-size:27px;
	font-family:"News";
	font-weight:300;
	border-bottom:1px solid black;
	padding-bottom:10px;
	left:-150px;
	width:1000px;
	background-color:rgb(0,0,0,0.1);
	padding-top:15px;

}

.price{
	position:relative;
	font-family:'News';
	font-weight:300;
	font-size:16px;
	

}
.BuyPanel img{
	position:relative;
	width:100px;
}


.label1{
	position:relative;
	font-size:25px;
	float:left;
	left:20px;
	font-weight:400;
	top:20px;
	height:150px;

}

.qty{
	font-size:20px;
	font-family:"rubik-bold";
	font-weight:300;
	position:relative;
	top:20px;

}


.buttonOrder{
	position:relative;
	background-color:white;
	width:300px;
	margin:0 auto;
	top:10px;
	cursor:pointer;
	border:1px solid black;
}

.buttonOrder:hover{
	border:1px solid black;
}