

.Header{
	position:relative;
	height:90px;
	width:100%;
	background-color:#03a9f3;
	border-bottom:1px solid grey;
	z-index:4;
	

	
}
a{
	color:black;
	text-decoration:none;
	
	
}
a:visited{
	color:white;
}

.logo-name{
	position:relative;
	float:left;
	font-family:'Cairo-Light';
	
	font-weight:700;
	font-size:34px;
	color:white;
	padding-left:20px;
	padding-top:2px;
	
	padding-left:46px;
	padding-top:2px;
	
	
}
.logo-name:hover{
	cursor:pointer;
}

.navigation{
	float:right;
	right:100px;
	position:relative;
	font-size:22px;
	font-family:"muli";
	font-weight:600;
	letter-spacing:3px;
	font-family:"Cairo-Light";
	font-weight:400;
	z-index: 5;
	overflow:hidden;


	
	
}

.button2{
	margin-left:60px;
	display:inline-block;
	margin-top:30px;
	color:white;

	padding:1px 8px 1px 12px;

	
}

.button2:hover{
	cursor:pointer;
	color:rgb(255,255,255,0.4);
}

.userinfo{
	margin-top:30px;
	color:rgb(255,255,255,0.8);
	display:inline-block;
	margin-left:120px;
	font-family:"Cairo-Light";
	font-weight:400;
}


#overlay {
  position: fixed; /* Sit on top of the page content */
  display: block; /* Hidden by default */
  width: 100%; /* Full width (cover the whole page) */
  height: 100%; /* Full height (cover the whole page) */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.5); /* Black background with opacity */
  z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
  cursor: pointer; /* Add a pointer on hover */
}
